.arrow {
  position: relative;
}
.scrollup {
  text-align: center;
  padding: 10px;
  position: fixed;
  right: 15px;
  bottom: 15px;
  //   background-color: rgba(255, 165, 186, 0.534);
  background-image: linear-gradient(to right, rgba(255, 165, 186, 0.7), rgba(255, 165, 186, 0.7));
  color: var(--text-light);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  display: none;
  z-index: 10;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  &:hover {
    // background-color: rgb(194, 226, 151, 0.7);
    background-image: linear-gradient(to right, rgba(194, 226, 151, 0.7), rgba(194, 226, 151, 0.7));
    fill: var(--accent-secondary);
  }
  &-show {
    display: block;
  }
}
.scrollup__icon {
  width: 30px;
  height: 30px;
  fill: currentColor;
}

.scrollup-show {
  display: block;
}
